const $projectSliderEl = $('[data-project-slider]');

const projectSlider = new Swiper($projectSliderEl, {
    slidesPerView: 1,
    resistanceRatio: 0,
    pagination: {
        el: '[data-project-slider-pag]',
    },
    navigation: {
        nextEl: '[data-project-slider-next]',
        prevEl: '[data-project-slider-prev]',
    },
})